import React from "react";
import { Layout, Menu } from "antd";
import { HashRouter as Router, Link, Route } from "react-router-dom";

import "./styles/reset.css";
import "./styles/base.css";
import Board from "./pages/Board";
import Records from "./pages/Records";
import { defaultRecord } from "./utils/constants";
import Home from "./pages/Home";
import Frogs from "./pages/Frogs";

const App: React.FC = () => {
  const [view, setView] = React.useState(window.location.hash);

  const record = JSON.parse(localStorage.getItem("playerRecord") || "{}");

  if (!record.modified) {
    localStorage.setItem("playerRecord", JSON.stringify(defaultRecord));
  }

  return (
    <Router>
      <Layout style={{ minHeight: "100vh" }}>
        <Layout.Sider theme="light">
          <Menu
            onSelect={({ key }) => setView(key)}
            selectedKeys={[view]}
            mode="inline"
          >
            <Menu.Item key="#/">
              <Link to="/">Home</Link>
            </Menu.Item>
            <Menu.Item key="#/board">
              <Link to="board">Board</Link>
            </Menu.Item>
            <Menu.Item key="#/records">
              <Link to="records">Records</Link>
            </Menu.Item>
          </Menu>
        </Layout.Sider>

        <Layout>
          <Layout.Content style={{ padding: "24px" }}>
            <Route
              component={() => {
                return <Home />;
              }}
              exact
              path="/"
            />
            <Route
              component={() => {
                return <Board record={record} />;
              }}
              exact
              path="/board"
            />
            <Route
              component={() => {
                return <Records record={record} />;
              }}
              exact
              path="/records"
            />
            <Route
              component={() => {
                return <Frogs />;
              }}
              exact
              path="/frogs"
            />
          </Layout.Content>
        </Layout>
      </Layout>
    </Router>
  );
};

export default App;
